import React,{ Component } from 'react'
import {BrowserRouter as Router,Route,Link,withRouter} from 'react-router-dom'//导入的方式跟之前有点变化

import './Article.less'

class ArticleHeader extends Component{
	constructor(props){
		super(props)
		this.state={
			isCurrent:0,
			category:["前端开发","后台开发","移动开发","相关工具","其它相关"],
		}
	}
	render(){
	    return (
	      <div className="articleContainer">
	         {this.state.category.map((item,i)=>
	           <span className={`nav ${this.state.isCurrent===i?'active':''}`} key={i}>{item}</span>
	         )}
	      </div>
	    )
    }
}

class Article extends Component{
	constructor(props){
		super(props)
		this.state={

		}
	}
	render(){
		return(
          <div>
            <ArticleHeader />
            <button onClick={()=>{this.props.history.push('/Lists')}}>点击跳转</button>
          </div>    
		)
	}
}


export default withRouter(Article)


